import React, { useState } from 'react';
import { UserService } from '../services/userService';
import { PasswordUtils } from '../utils/passwordUtils';

interface PasswordChangeModalProps {
  isOpen: boolean;
  userId: number;
  onClose: () => void;
  onSuccess: () => void;
}

const PasswordChangeModal: React.FC<PasswordChangeModalProps> = ({
  isOpen,
  userId,
  onClose,
  onSuccess
}) => {
  const [currentPassword, setCurrentPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    //  
    if (!currentPassword || !newPassword || !confirmPassword) {
      setError('  .');
      return;
    }

    if (newPassword !== confirmPassword) {
      setError('   .');
      return;
    }

    if (currentPassword === newPassword) {
      setError('     .');
      return;
    }

    const validation = PasswordUtils.validatePassword(newPassword);
    if (!validation.valid) {
      setError(validation.errors.join(' '));
      return;
    }

    setIsLoading(true);

    try {
      //   
      const user = await UserService.getUserById(userId);
      if (!user || !user.password_hash) {
        setError('    .');
        return;
      }

      const isCurrentValid = await PasswordUtils.verifyPassword(currentPassword, user.password_hash);
      if (!isCurrentValid) {
        setError('   .');
        return;
      }

      //  
      await UserService.changePassword(userId, newPassword);

      alert('  .');
      onSuccess();
      onClose();
    } catch (err) {
      setError('    .');
      console.error('Password change error:', err);
    } finally {
      setIsLoading(false);
    }
  };

  const handleClose = () => {
    setCurrentPassword('');
    setNewPassword('');
    setConfirmPassword('');
    setError('');
    onClose();
  };

  if (!isOpen) return null;

  return (
    <div className="modal-overlay">
      <div className="modal-content password-change-modal">
        <div className="modal-header">
          <h2>  </h2>
          <button onClick={handleClose} className="modal-close"></button>
        </div>

        <form onSubmit={handleSubmit} className="password-change-form">
          {error && (
            <div className="error-message">
              <span className="error-icon"></span>
              {error}
            </div>
          )}

          <div className="form-group">
            <label htmlFor="currentPassword">  *</label>
            <input
              type="password"
              id="currentPassword"
              value={currentPassword}
              onChange={(e) => setCurrentPassword(e.target.value)}
              placeholder="  "
              disabled={isLoading}
            />
          </div>

          <div className="form-group">
            <label htmlFor="newPassword">  *</label>
            <input
              type="password"
              id="newPassword"
              value={newPassword}
              onChange={(e) => setNewPassword(e.target.value)}
              placeholder="  "
              disabled={isLoading}
            />
            <p className="password-help">
               4 ,  20  .
            </p>
          </div>

          <div className="form-group">
            <label htmlFor="confirmPassword">   *</label>
            <input
              type="password"
              id="confirmPassword"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              placeholder="   "
              disabled={isLoading}
            />
          </div>

          <div className="form-actions">
            <button
              type="button"
              onClick={handleClose}
              className="secondary-button"
              disabled={isLoading}
            >
              
            </button>
            <button
              type="submit"
              className="primary-button"
              disabled={isLoading}
            >
              {isLoading ? ' ...' : ' '}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default PasswordChangeModal;