import { getDatabase } from '../database/database';

export const insertDummyData = () => {
  const db = getDatabase();

  try {
    //   
    const ingredientCountStmt = db.prepare('SELECT COUNT(*) as count FROM ingredients');
    ingredientCountStmt.step();
    const existingIngredients = ingredientCountStmt.getAsObject() as { count: number };
    ingredientCountStmt.free();

    const menuCountStmt = db.prepare('SELECT COUNT(*) as count FROM menus');
    menuCountStmt.step();
    const existingMenus = menuCountStmt.getAsObject() as { count: number };
    menuCountStmt.free();

    if (existingIngredients.count > 0 || existingMenus.count > 0) {
      console.log('   . .');
      return;
    }

    //  
    const ingredients = [
      { name: '', unit: 'g' },
      { name: '', unit: 'ml' },
      { name: '', unit: 'g' },
      { name: '', unit: 'ml' },
      { name: '', unit: 'ml' },
      { name: '', unit: 'ml' },
      { name: '', unit: 'ml' },
      { name: '', unit: 'g' },
      { name: '', unit: 'g' },
      { name: '', unit: 'g' }
    ];

    const ingredientStmt = db.prepare('INSERT INTO ingredients (name, unit) VALUES (?, ?)');
    ingredients.forEach(ingredient => {
      ingredientStmt.run([ingredient.name, ingredient.unit]);
    });
    ingredientStmt.free();

    //  
    const menus = [
      { name: '', description: '   ', price: 4000 },
      { name: '', description: '   ', price: 4500 },
      { name: '', description: '   ', price: 4500 },
      { name: '', description: '   ', price: 5000 },
      { name: '', description: '  ', price: 5500 },
      { name: '', description: '   ', price: 6000 },
      { name: '', description: '  ', price: 5500 }
    ];

    const menuStmt = db.prepare('INSERT INTO menus (name, description, price) VALUES (?, ?, ?)');
    menus.forEach(menu => {
      menuStmt.run([menu.name, menu.description, menu.price]);
    });
    menuStmt.free();

    //   (- )
    const recipes = [
      //  ( ID 1)
      { menu_id: 1, ingredient_id: 1, quantity: 20 }, //  20g

      //  ( ID 2)
      { menu_id: 2, ingredient_id: 1, quantity: 18 }, //  18g
      { menu_id: 2, ingredient_id: 2, quantity: 150 }, //  150ml

      //  ( ID 3)
      { menu_id: 3, ingredient_id: 1, quantity: 18 }, //  18g
      { menu_id: 3, ingredient_id: 2, quantity: 120 }, //  120ml
      { menu_id: 3, ingredient_id: 5, quantity: 30 }, //  30ml

      //  ( ID 4)
      { menu_id: 4, ingredient_id: 1, quantity: 18 }, //  18g
      { menu_id: 4, ingredient_id: 2, quantity: 150 }, //  150ml
      { menu_id: 4, ingredient_id: 4, quantity: 20 }, //  20ml

      //  ( ID 5)
      { menu_id: 5, ingredient_id: 1, quantity: 18 }, //  18g
      { menu_id: 5, ingredient_id: 2, quantity: 150 }, //  150ml
      { menu_id: 5, ingredient_id: 7, quantity: 25 }, //  25ml
      { menu_id: 5, ingredient_id: 5, quantity: 20 }, //  20ml

      //  ( ID 6)
      { menu_id: 6, ingredient_id: 8, quantity: 100 }, //  100g
      { menu_id: 6, ingredient_id: 2, quantity: 200 }, //  200ml
      { menu_id: 6, ingredient_id: 3, quantity: 15 }, //  15g
      { menu_id: 6, ingredient_id: 10, quantity: 100 }, //  100g

      //  ( ID 7)
      { menu_id: 7, ingredient_id: 9, quantity: 120 }, //  120g
      { menu_id: 7, ingredient_id: 2, quantity: 200 }, //  200ml
      { menu_id: 7, ingredient_id: 3, quantity: 10 }, //  10g
      { menu_id: 7, ingredient_id: 10, quantity: 100 } //  100g
    ];

    const recipeStmt = db.prepare('INSERT INTO recipes (menu_id, ingredient_id, quantity) VALUES (?, ?, ?)');
    recipes.forEach(recipe => {
      recipeStmt.run([recipe.menu_id, recipe.ingredient_id, recipe.quantity]);
    });
    recipeStmt.free();

    //  
    const inventories = [
      { ingredient_id: 1, current_stock: 5000, min_stock: 500 }, // 
      { ingredient_id: 2, current_stock: 10000, min_stock: 1000 }, // 
      { ingredient_id: 3, current_stock: 2000, min_stock: 200 }, // 
      { ingredient_id: 4, current_stock: 1500, min_stock: 100 }, // 
      { ingredient_id: 5, current_stock: 2000, min_stock: 200 }, // 
      { ingredient_id: 6, current_stock: 1200, min_stock: 100 }, // 
      { ingredient_id: 7, current_stock: 1300, min_stock: 100 }, // 
      { ingredient_id: 8, current_stock: 3000, min_stock: 300 }, // 
      { ingredient_id: 9, current_stock: 2500, min_stock: 250 }, // 
      { ingredient_id: 10, current_stock: 50000, min_stock: 5000 } // 
    ];

    const inventoryStmt = db.prepare('INSERT INTO inventory (ingredient_id, current_stock, min_stock) VALUES (?, ?, ?)');
    inventories.forEach(inventory => {
      inventoryStmt.run([inventory.ingredient_id, inventory.current_stock, inventory.min_stock]);
    });
    inventoryStmt.free();

    //    (  )
    const sampleOrders = [
      { payment_type: 'CARD', total_amount: 4000 },
      { payment_type: 'BAEMIN', total_amount: 9000 },
      { payment_type: 'COUPANG', total_amount: 5500 },
      { payment_type: 'YOGIYO', total_amount: 12000 },
      { payment_type: 'CARD', total_amount: 6000 }
    ];

    sampleOrders.forEach((order, index) => {
      const orderDate = new Date();
      orderDate.setDate(orderDate.getDate() - index); //    

      //   
      let businessDays = 0;
      switch (order.payment_type) {
        case 'CARD':
          businessDays = 2;
          break;
        case 'COUPANG':
        case 'BAEMIN':
        case 'YOGIYO':
          businessDays = 5;
          break;
      }

      const expectedDate = new Date(orderDate);
      let addedDays = 0;
      while (addedDays < businessDays) {
        expectedDate.setDate(expectedDate.getDate() + 1);
        if (expectedDate.getDay() !== 0 && expectedDate.getDay() !== 6) {
          addedDays++;
        }
      }

      const orderStmt = db.prepare(`
        INSERT INTO orders (order_date, total_amount, payment_type, expected_deposit_date, is_deposited)
        VALUES (?, ?, ?, ?, ?)
      `);
      orderStmt.run([
        orderDate.toISOString(),
        order.total_amount,
        order.payment_type,
        expectedDate.toISOString().split('T')[0],
        index > 2 ? 0 : 1 //  3  ,  
      ]);
      orderStmt.free();
    });

    console.log('    !');
    console.log('    src/data/dummyData.ts  ');
    console.log('   App.tsx insertDummyData()   .');

  } catch (error) {
    console.error('      :', error);
  }
};

//     (  )
export const clearDummyData = () => {
  const db = getDatabase();

  try {
    //      
    const deleteStatements = [
      'DELETE FROM inventory_history',
      'DELETE FROM order_items',
      'DELETE FROM orders',
      'DELETE FROM recipes',
      'DELETE FROM inventory',
      'DELETE FROM menus',
      'DELETE FROM ingredients'
    ];

    deleteStatements.forEach(sql => {
      const stmt = db.prepare(sql);
      stmt.run();
      stmt.free();
    });

    console.log('    .');
  } catch (error) {
    console.error('      :', error);
  }
};