import React, { useState, useEffect } from 'react';
import { Order, OrderItemWithDetails } from '../types';
import { OrderService } from '../services/orderService';
import { MenuService } from '../services/menuService';
import { InventoryService } from '../services/inventoryService';

interface OrderManagementProps {
  onOrderUpdate?: () => void;
}

const OrderManagement: React.FC<OrderManagementProps> = ({ onOrderUpdate }) => {
  const [orders, setOrders] = useState<Order[]>([]);
  const [selectedOrder, setSelectedOrder] = useState<Order | null>(null);
  const [orderItems, setOrderItems] = useState<OrderItemWithDetails[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  useEffect(() => {
    loadOrders();
  }, []);

  const loadOrders = () => {
    try {
      const orderData = OrderService.getAllOrders();
      //   
      setOrders(orderData.sort((a, b) =>
        new Date(b.order_date || 0).getTime() - new Date(a.order_date || 0).getTime()
      ));
    } catch (err) {
      setError('   .');
    }
  };

  const loadOrderItems = (orderId: number) => {
    try {
      const items = OrderService.getOrderItemsWithDetails(orderId);
      setOrderItems(items);
    } catch (err) {
      setError('    .');
    }
  };

  const selectOrder = (order: Order) => {
    setSelectedOrder(order);
    loadOrderItems(order.id!);
  };

  const cancelOrder = async (order: Order) => {
    const confirmMessage = `
 #${order.id} ?

      .
    .`;

    if (!window.confirm(confirmMessage)) {
      return;
    }

    setLoading(true);
    setError('');

    try {
      //      
      const items = OrderService.getOrderItemsWithDetails(order.id!);

      for (const item of items) {
        const recipes = MenuService.getRecipesByMenuId(item.menu_id);

        for (const recipe of recipes) {
          const returnAmount = recipe.quantity * item.quantity;
          InventoryService.adjustStock(
            recipe.ingredient_id,
            returnAmount,
            'IN',
            `  #${order.id} - ${item.menu_name}`
          );
        }
      }

      //  
      OrderService.deleteOrder(order.id!);

      setSuccess('  .');
      loadOrders();

      if (selectedOrder?.id === order.id) {
        setSelectedOrder(null);
        setOrderItems([]);
      }

      if (onOrderUpdate) {
        onOrderUpdate();
      }

    } catch (err) {
      setError(err instanceof Error ? err.message : '    .');
    } finally {
      setLoading(false);
    }
  };

  const modifyOrderItemQuantity = async (
    orderId: number,
    itemId: number,
    menuId: number,
    currentQuantity: number,
    newQuantity: number
  ) => {
    if (newQuantity <= 0) {
      await removeOrderItem(orderId, itemId, menuId, currentQuantity);
      return;
    }

    const quantityDiff = newQuantity - currentQuantity;
    const confirmMessage = quantityDiff > 0
      ? ` ${currentQuantity} ${newQuantity} ?\n(  )`
      : ` ${currentQuantity} ${newQuantity} ?\n( )`;

    if (!window.confirm(confirmMessage)) {
      return;
    }

    setLoading(true);
    setError('');

    try {
      //  
      const recipes = MenuService.getRecipesByMenuId(menuId);

      for (const recipe of recipes) {
        const stockChange = recipe.quantity * Math.abs(quantityDiff);

        if (quantityDiff > 0) {
          //   -  
          const inventory = InventoryService.getInventoryByIngredientId(recipe.ingredient_id);
          if (!inventory || inventory.current_stock < stockChange) {
            setError(` : ${recipe.ingredient_name} (: ${stockChange}, : ${inventory?.current_stock || 0})`);
            setLoading(false);
            return;
          }

          InventoryService.adjustStock(
            recipe.ingredient_id,
            stockChange,
            'OUT',
            `  #${orderId} -  `
          );
        } else {
          //   -  
          InventoryService.adjustStock(
            recipe.ingredient_id,
            stockChange,
            'IN',
            `  #${orderId} -  `
          );
        }
      }

      //    
      OrderService.updateOrderItemQuantity(itemId, newQuantity);

      //   
      const updatedItems = OrderService.getOrderItemsWithDetails(orderId);
      const newTotal = updatedItems.reduce((sum, item) => sum + (item.quantity * item.unit_price), 0);
      OrderService.updateOrderTotal(orderId, newTotal);

      setSuccess('  .');
      loadOrders();
      loadOrderItems(orderId);

      if (onOrderUpdate) {
        onOrderUpdate();
      }

    } catch (err) {
      setError(err instanceof Error ? err.message : '    .');
    } finally {
      setLoading(false);
    }
  };

  const removeOrderItem = async (orderId: number, itemId: number, menuId: number, quantity: number) => {
    if (!window.confirm('   ?\n .')) {
      return;
    }

    setLoading(true);
    setError('');

    try {
      //  
      const recipes = MenuService.getRecipesByMenuId(menuId);

      for (const recipe of recipes) {
        const returnAmount = recipe.quantity * quantity;
        InventoryService.adjustStock(
          recipe.ingredient_id,
          returnAmount,
          'IN',
          `   #${orderId}`
        );
      }

      //   
      OrderService.deleteOrderItem(itemId);

      //   
      const updatedItems = OrderService.getOrderItemsWithDetails(orderId);
      if (updatedItems.length === 0) {
        //     
        OrderService.deleteOrder(orderId);
        setSelectedOrder(null);
        setOrderItems([]);
      } else {
        const newTotal = updatedItems.reduce((sum, item) => sum + (item.quantity * item.unit_price), 0);
        OrderService.updateOrderTotal(orderId, newTotal);
        loadOrderItems(orderId);
      }

      setSuccess('  .');
      loadOrders();

      if (onOrderUpdate) {
        onOrderUpdate();
      }

    } catch (err) {
      setError(err instanceof Error ? err.message : '    .');
    } finally {
      setLoading(false);
    }
  };

  const formatDate = (dateString?: string) => {
    if (!dateString) return ' ';
    return new Date(dateString).toLocaleString('ko-KR', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  return (
    <div className="order-management">
      <div className="management-header">
        <h2>  </h2>
        {error && <div className="alert alert-error">{error}</div>}
        {success && <div className="alert alert-success">{success}</div>}
      </div>

      <div className="management-content">
        {/*   */}
        <div className="orders-list">
          <h3>  ({orders.length})</h3>
          {orders.length === 0 ? (
            <div className="empty-state">
              <p> .</p>
            </div>
          ) : (
            <div className="orders-grid">
              {orders.map(order => (
                <div
                  key={order.id}
                  className={`order-card ${selectedOrder?.id === order.id ? 'selected' : ''}`}
                  onClick={() => selectOrder(order)}
                >
                  <div className="order-header">
                    <span className="order-id"> #{order.id}</span>
                    <span className="order-date">{formatDate(order.order_date)}</span>
                  </div>
                  <div className="order-amount">
                    {order.total_amount.toLocaleString()}
                  </div>
                  <button
                    className="btn btn-danger btn-small"
                    onClick={(e) => {
                      e.stopPropagation();
                      cancelOrder(order);
                    }}
                    disabled={loading}
                  >
                    
                  </button>
                </div>
              ))}
            </div>
          )}
        </div>

        {/*   */}
        {selectedOrder && (
          <div className="order-details">
            <h3>  - #{selectedOrder.id}</h3>
            <div className="order-info">
              <p><strong> :</strong> {formatDate(selectedOrder.order_date)}</p>
              <p><strong> :</strong> {selectedOrder.total_amount.toLocaleString()}</p>
            </div>

            <div className="order-items">
              <h4> </h4>
              {orderItems.map(item => (
                <div key={item.id} className="order-item">
                  <div className="item-info">
                    <h5>{item.menu_name}</h5>
                    <p>: {item.unit_price.toLocaleString()}</p>
                  </div>
                  <div className="item-controls">
                    <div className="quantity-controls">
                      <button
                        className="btn btn-small"
                        onClick={() => modifyOrderItemQuantity(
                          selectedOrder.id!,
                          item.id!,
                          item.menu_id,
                          item.quantity,
                          item.quantity - 1
                        )}
                        disabled={loading}
                      >
                        -
                      </button>
                      <span className="quantity">{item.quantity}</span>
                      <button
                        className="btn btn-small"
                        onClick={() => modifyOrderItemQuantity(
                          selectedOrder.id!,
                          item.id!,
                          item.menu_id,
                          item.quantity,
                          item.quantity + 1
                        )}
                        disabled={loading}
                      >
                        +
                      </button>
                    </div>
                    <button
                      className="btn btn-danger btn-small"
                      onClick={() => removeOrderItem(
                        selectedOrder.id!,
                        item.id!,
                        item.menu_id,
                        item.quantity
                      )}
                      disabled={loading}
                    >
                      
                    </button>
                  </div>
                  <div className="item-total">
                    {(item.quantity * item.unit_price).toLocaleString()}
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}
      </div>

      <style>{`
        .order-management {
          max-width: 1200px;
          margin: 0 auto;
          padding: 2rem;
        }

        .management-header {
          margin-bottom: 2rem;
        }

        .management-header h2 {
          margin: 0 0 1rem 0;
          color: #333;
        }

        .alert {
          padding: 0.75rem 1rem;
          border-radius: 4px;
          margin-bottom: 1rem;
        }

        .alert-error {
          background-color: #ffeaea;
          color: #d32f2f;
          border: 1px solid #ffcdd2;
        }

        .alert-success {
          background-color: #e8f5e8;
          color: #4caf50;
          border: 1px solid #c8e6c9;
        }

        .management-content {
          display: grid;
          grid-template-columns: 1fr 1fr;
          gap: 2rem;
        }

        .orders-list h3, .order-details h3 {
          margin: 0 0 1rem 0;
          color: #333;
        }

        .empty-state {
          text-align: center;
          padding: 2rem;
          color: #999;
          background: white;
          border-radius: 8px;
          border: 1px solid #eee;
        }

        .orders-grid {
          display: flex;
          flex-direction: column;
          gap: 1rem;
        }

        .order-card {
          background: white;
          border: 1px solid #ddd;
          border-radius: 8px;
          padding: 1rem;
          cursor: pointer;
          transition: all 0.2s;
          display: flex;
          justify-content: space-between;
          align-items: center;
        }

        .order-card:hover {
          border-color: #2196f3;
          box-shadow: 0 2px 8px rgba(33, 150, 243, 0.2);
        }

        .order-card.selected {
          border-color: #2196f3;
          background-color: #f3f9ff;
          box-shadow: 0 2px 8px rgba(33, 150, 243, 0.3);
        }

        .order-header {
          display: flex;
          flex-direction: column;
          gap: 0.25rem;
        }

        .order-id {
          font-weight: bold;
          color: #333;
        }

        .order-date {
          font-size: 0.85rem;
          color: #666;
        }

        .order-amount {
          font-size: 1.1rem;
          font-weight: bold;
          color: #2196f3;
        }

        .order-details {
          background: white;
          border-radius: 8px;
          padding: 1.5rem;
          box-shadow: 0 2px 8px rgba(0,0,0,0.1);
        }

        .order-info {
          background: #f8f9fa;
          padding: 1rem;
          border-radius: 4px;
          margin-bottom: 1.5rem;
        }

        .order-info p {
          margin: 0.5rem 0;
        }

        .order-items h4 {
          margin: 0 0 1rem 0;
          color: #333;
        }

        .order-item {
          display: grid;
          grid-template-columns: 1fr auto auto;
          gap: 1rem;
          align-items: center;
          padding: 1rem 0;
          border-bottom: 1px solid #eee;
        }

        .order-item:last-child {
          border-bottom: none;
        }

        .item-info h5 {
          margin: 0 0 0.25rem 0;
          font-size: 1rem;
        }

        .item-info p {
          margin: 0;
          color: #666;
          font-size: 0.9rem;
        }

        .item-controls {
          display: flex;
          flex-direction: column;
          gap: 0.5rem;
          align-items: center;
        }

        .quantity-controls {
          display: flex;
          align-items: center;
          gap: 0.5rem;
        }

        .quantity {
          min-width: 2rem;
          text-align: center;
          font-weight: bold;
        }

        .item-total {
          font-weight: bold;
          color: #2196f3;
          text-align: right;
        }

        .btn {
          border: none;
          border-radius: 4px;
          padding: 0.5rem 1rem;
          cursor: pointer;
          font-size: 0.9rem;
          transition: all 0.2s;
          font-weight: 500;
        }

        .btn:hover:not(:disabled) {
          transform: translateY(-1px);
          box-shadow: 0 2px 8px rgba(0,0,0,0.2);
        }

        .btn:disabled {
          opacity: 0.6;
          cursor: not-allowed;
        }

        .btn-danger {
          background: #f44336;
          color: white;
        }

        .btn-small {
          padding: 0.25rem 0.5rem;
          font-size: 0.8rem;
        }

        @media (max-width: 768px) {
          .management-content {
            grid-template-columns: 1fr;
          }

          .order-card {
            flex-direction: column;
            align-items: flex-start;
            gap: 0.5rem;
          }

          .order-item {
            grid-template-columns: 1fr;
            gap: 0.5rem;
          }

          .item-controls {
            flex-direction: row;
            justify-content: space-between;
            width: 100%;
          }
        }
      `}</style>
    </div>
  );
};

export default OrderManagement;